.App {
  max-width: 1800px;
  margin: 0 auto;
  padding: 10px;
}

main {
  display: flex;
  flex-flow: row wrap;
}

.garmet {
  max-width: 280px;
  width: 100%;
  padding: 1rem;
  margin: 1rem;
  border: 1px solid grey;
}

.garmetImg {
  max-width: 120px;
  width: 100%;
  height: auto;
}

.garmet.garmet--active {
  max-width: 560px;
  width: 100%;
  padding: 1rem;
  margin: 1rem;
  border: 1px solid grey;
  display: flex;
  flex-flow: row wrap;
}

.garmetCard {
  flex: 1 0 180px;
}

.quoteBuilder {
  flex: 1 0 180px;
}

/* from formik demo */

* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Helvetica, Arial,
    sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  font-size: 14px;
  line-height: 1.5;
  color: #24292e;
  background-color: #fff;
}

a {
  color: #08c;
}

input {
  padding: .5rem;
  font-size: 16px;
  width: 100%;
  display: block;
  border-radius: 4px;
  border: 1px solid #ccc;
  box-sizing: border-box;
}

input:focus {
  border-color: #007eff;
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075),
    0 0 0 3px rgba(0, 126, 255, 0.1);
  outline: none;
}

input[type="checkbox"],
input[type="radio"],
input[type="checkbox"] + label,
input[type="radio"] + label {
  display: inline-block;
  width: auto;
}


input.error {
  border-color: red;
}

label {
  font-weight: bold;
  display: block;
  margin-bottom: .5rem;
}

.input-feedback {
  color: red;
  margin-top: .25rem;
}

button {
  max-width: 150px;
  margin: 20px 0;
  padding: 12px 20px;
  border-style: none;
  border-radius: 5px;
  background-color: #08c;
  box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.15);
  font-size: 17px;
  font-weight: 500;
  color: #fff;
  cursor: pointer;
  outline: none;
  -webkit-appearance: none;
}

button:disabled {
  opacity: .5;
  cursor: not-allowed !important;
}

button + button {
  margin-left: .5rem;
}

button.outline {
  background-color: #eee;
  border: 1px solid #aaa;
  color: #555;
}
